/*   ---------------------------   INSTAGRAM   ---------------------------   */

.instagram {
    display: flex;
    flex-direction: column;
}

.instagram__top {
    margin: 0 auto;
    padding: 44px 0 45px;
}

.instagram__top-link {
    display: flex;
    align-items: center;
}

.instagram__top-icon {
    display: flex;
    width: 26px;
    height: 26px;
    margin-right: 12px;
}

.instagram__top-title {
    font-size: 1.5rem;
    text-align: left;
    color: #0aa566;
}

.instagram__content {
    margin-bottom: 11px;
    display: flex;
    justify-content: center;
}

.instagram__link {
    position: relative;
    width: 25%;
    max-height: 384px;
}

.instagram__link::before {
    content: "";
    background-image: url('./../images/inst-wt-ic.png');
    background-size: 25px 25px ;
    width: 25px;
    height: 25px;
    position: absolute;
    bottom: 20px;
    right: 22px;
    z-index: 1;
}
.instagram__link::after {
    content: "";
    background: linear-gradient(to top left, #0000006c 10%, #0000000c 30%, #a1988700 70%) ;
    width: 150px;
    height: 150px;
    position: absolute;
    bottom: 0px;
    right: 0px;
}

.instagram__image {
    width:100%;
    height: 100%;
    object-fit: cover;
}